<template>
    <div class="api">
        <h2>IndexedDB API</h2>
        <div class="api-info">
            <h3>IndexedDB API</h3>
            <p>IndexedDB is an API for client-side storage of significant amounts of structured data and for high performance searches on this data using indexes. While DOM Storage is useful for storing smaller amounts of data, it is less useful for storing larger amounts of structured data. IndexedDB provides a solution.</p>
        </div>
        <div class="api-info">
            <h3>Environment (IDBEnvironment and IDBFactory)</h3>
            <p>The IDBEvironment interface of the IndexedDB API provides asynchronous access to a client-side database. It is implemented by window and worker objects.</p>
            <p>The IDBFactory interface of the IndexedDB API lets applications asynchronously access the indexed databases. The object that implements the interface is window.indexedDB. You open—that is, create and access—and delete a database with the object and not directly with IDBFactory.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>open(name)</td>
                <td>This method opens a database with the name specified in its attributes. If no previous database exists, a new one is created using the name provided.</td>
            </tr>
            <tr>
                <td>deleteDatabase(name)</td>
                <td>This method deletes the database with the name specified in its attributes.</td>
            </tr>
        </table>
        <pre class="api-code">
	indexedDB.open(dbname); 
	indexedDB.deleteDatabase(dbname);
	</pre>
        <h4 class="api-table-caption">The Attributes</h4>
        <table class="api-table">
            <tr>
                <td>indexedDB</td>
                <td>This attribute provides a mechanism for accessing the indexed database system.</td>
            </tr>
        </table>
        <pre class="api-code">
	window.indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB;
	</pre>
        <div class="api-info">
            <h3>Database (IDBDatabase)</h3>
            <p>The IDBDatabase interface of the IndexedDB API provides asynchronous access to a connection to a database. Use it to create, manipulate, and delete objects in that database.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>setVersion(value)</td>
                <td>This method sets a new version for the opened database. The value attribute can be any string we want.</td>
            </tr>
            <tr>
                <td>createObjectStore(name, keyPath, autoIncrement)</td>
                <td>This method creates a new Object Store for the opened database. The name attribute represents the name of the Object Store, keyPath is a common index for the objects stored in this Object Store and autoIncrement is a boolean value to activate a key generator.</td>
            </tr>
            <tr>
                <td>deleteObjectStore(name)</td>
                <td>This method deletes an Object Store with the name specified in its attribute.</td>
            </tr>
            <tr>
                <td>transaction(stores, type, timeout)</td>
                <td>This method initiates a transaction. The transaction can be specific for one or more Object Stores declared in the stores attribute, and it can be created for different access modes according to the type attribute. It can also receive a timeout attribute in milliseconds to specify the time the operation is allowed to take.</td>
            </tr>
        </table>
        <pre class="api-code">
	//db - is IDBDatabase object 
	db.setVersion(db_version); 

	db.createObjectStore(object_name, {keyPath: "timeStamp"}); 

	var trans = db.transaction([object_name], "readwrite");
	</pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>version</td>
                <td>This property returns the value of the current version of the open database.</td>
            </tr>
            <tr>
                <td>name</td>
                <td>This property returns the name of the open database.</td>
            </tr>
            <tr>
                <td>objectStoreNames</td>
                <td>This property returns a list of the names of the Object Stores in the opened database.</td>
            </tr>
        </table>
        <pre class="api-code">
	//db - is IDBDatabase object 
	var version=db.version;
	var name=db.name;
	var list=db.objectStoreNames;
	</pre>
        <div class="api-info">
            <h3>Object Store (IDBObjectStore)</h3>
            <p>The IDBObjectStore interface of the IndexedDB API represents an object store in a database. Records within an object store are sorted according to their keys. This sorting enables fast insertion, look-up, and ordered retrieval.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>add(object)</td>
                <td>This method adds an object to the selected Object Store with the information provided in its attributes. If an object with the same index already exists, an error is returned. The method can receive a keyword/value pair or an object containing several keyword/value pairs as attribute.</td>
            </tr>
            <tr>
                <td>put(object)</td>
                <td>This method adds an object to the selected Object Store with the information provided in its attributes. If an object with the same index already exists, the object is overwritten with the new information. The method can receive a keyword/value pair or an object containing several keyword/value pairs as attribute.</td>
            </tr>
            <tr>
                <td>get(key)</td>
                <td>This method returns the object with the index corresponding to key.</td>
            </tr>
            <tr>
                <td>delete(key)</td>
                <td>This method deletes the object with the index corresponding to key.</td>
            </tr>
            <tr>
                <td>createIndex(name, property, unique)</td>
                <td>This method creates a new index for the selected Object Store. The name attribute specifies the name of the index, the property attribute declares the property of the objects that will be associated with this index and the unique attribute indicates whether objects with the same index value are allowed or not.</td>
            </tr>
            <tr>
                <td>index(name)</td>
                <td>This method opens the index with the name specified in its attribute.</td>
            </tr>
            <tr>
                <td>deleteIndex(name)</td>
                <td>This method deletes an index with the name specified in its attribute.</td>
            </tr>
            <tr>
                <td>openCursor(range, direction)</td>
                <td>This method creates a cursor over the object of the selected Object Store. The range attribute takes a range object to determine which objects are selected. The direction attribute set the order of these objects.</td>
            </tr>
        </table>
        <pre class="api-code">
	var trans = db.transaction([object_name], "readwrite");
	var store = trans.objectStore(object_name);
	var timeStamp=new Date().getTime();
	var data = {"record": 10,"timeStamp": timeStamp};
	var request = store.put(data);
	// or var request = store.add(data); 

	var request = store.get(1); // request.record is equal 10 
	var request = store.delete(1); 

	var trans = db.transaction([object_name], "readwrite");
	var store = trans.objectStore(object_name);
	var cursorRequest = store.openCursor();
	</pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>name</td>
                <td>This property provides the name of the Object Store currently in use.</td>
            </tr>
            <tr>
                <td>keyPath</td>
                <td>This property returns the keyPath, if any, for the Object Store currently in use.</td>
            </tr>
            <tr>
                <td>IndexNames</td>
                <td>This property returns a list of the names of the indexes created for the Object Store currently in use.</td>
            </tr>
        </table>
        <pre class="api-code">
	var data = {"record": {"foo": "bar"},"timeStamp": timeStamp}; // keyPath - "timeStamp" 

	var trans = db.transaction([object_name], "readwrite");
	var store = trans.objectStore(object_name);
	var indexnames=store.IndexNames;
	</pre>
        <div class="api-info">
            <h3>Cursors(IDBCursor)</h3>
            <p>The IDBCursor interface of the IndexedDB API represents a cursor for traversing or iterating over multiple records in a database.</p>
        </div>
        <h4 class="api-table-caption">The Constants</h4>
        <table class="api-table">
            <tr>
                <td>NEXT</td>
                <td>This constant determines an ascending order for the objects pointed to by the cursor (This is the value by default.).</td>
            </tr>
            <tr>
                <td>NEXT_NO_DUPLICATE</td>
                <td>This constant determines an ascending order for the objects pointed to by the cursor and ignores the duplicates.</td>
            </tr>
            <tr>
                <td>PREV</td>
                <td>This constant determines a descending order for the objects pointed to by the cursor.</td>
            </tr>
            <tr>
                <td>PREV_NO_DUPLICATE</td>
                <td>This constant determines a descending order for the objects pointed to by the cursor and ignores the duplicates.</td>
            </tr>
        </table>
        <pre class="api-code">
	var trans = db.transaction([object_name], "readwrite");
	var store = trans.objectStore(object_name);
	var cursorRequest = store.openCursor(range, IDBCursorSync.NEXT_NO_DUPLICATE);
	var cursorRequest = store.openCursor("prev");
	</pre>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>continue(key)</td>
                <td>This method moves the pointer of the cursor to the next object on the list or to the object referenced by the key attribute, if it’s present.</td>
            </tr>
            <tr>
                <td>delete()</td>
                <td>This method deletes the object currently pointed to by the cursor.</td>
            </tr>
            <tr>
                <td>update(value)</td>
                <td>This method updates the object currently pointed to by the cursor with the value provided in its attribute.</td>
            </tr>
        </table>
        <pre class="api-code">
	cursorRequest.onsuccess=function(e){ 
	   var cursor=cursorRequest.result || e.result;
	    ...
	   cursor.continue(); 
	}
	</pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>key</td>
                <td>This property returns the value of the index for the object currently pointed to by the cursor.</td>
            </tr>
            <tr>
                <td>value</td>
                <td>This property returns the value of any property of the object currently pointed to by the cursor.</td>
            </tr>
            <tr>
                <td>direction</td>
                <td>This property returns the order for the objects read by the cursor (ascending or descending).</td>
            </tr>
        </table>
        <pre class="api-code">
	cursorRequest.onsuccess=function(e){ 
	   var cursor=cursorRequest.result || e.result;
	    var datavalue=cursor.value; // object
	    var datakey=cursor.key;
	    var datadirection=cursor.direction;
	   cursor.continue(); 
	}
	</pre>
        <div class="api-info">
            <h3>Transactions (IDBTransaction)</h3>
            <p>The IDBTransaction interface of the IndexedDB API provides a static, asynchronous transaction on a database using event handler attributes. All reading and writing of data are done within transactions. You actually use IDBDatabase to start transactions and use IDBTransaction to set the mode of the transaction and access an object store and make your request. You can also use it to abort transactions.</p>
        </div>
        <h4 class="api-table-caption">The Constants (Types)</h4>
        <table class="api-table">
            <tr>
                <td>readonly</td>
                <td>This constant configures the transaction as a read_only transaction (This is the value by default.).</td>
            </tr>
            <tr>
                <td>readwrite</td>
                <td>This constant configures the transaction as a read_write transaction.</td>
            </tr>
            <tr>
                <td>versionchange</td>
                <td>This type of transaction is used solely for updating the version number.</td>
            </tr>
        </table>
        <pre class="api-code">
	var trans = db.transaction([object_name], "readwrite");
	</pre>
        <div class="api-info">
            <h3>Range (IDBKeyRange)</h3>
            <p>The IDBKeyRange interface of the IndexedDB API represents a continuous interval over some data type that is used for keys. Records can be retrieved from object stores and indexes using keys or a range of keys. You can limit the range using lower and upper bounds. For example, you can iterate over all values of a key between x and y.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>only(value)</td>
                <td>This method returns a range with both the ending and starting point set to value.</td>
            </tr>
            <tr>
                <td>bound(lower, upper, lowerOpen, upperOpen)</td>
                <td>This method returns a range with the starting point set by lower, the ending point set by upper and whether this values will be excluded from the list of objects or not.</td>
            </tr>
            <tr>
                <td>lowerBound(value, open)</td>
                <td>This method returns a range starting from value and ending at the end of the object list. The open attribute determines whether the objects that match value will be excluded or not.</td>
            </tr>
            <tr>
                <td>upperBound(value, open)</td>
                <td>This method returns a range starting from the beginning of the object list and ending at value. The open attribute determines whether the objects that match value will be excluded or not.</td>
            </tr>
        </table>
        <pre class="api-code">
	store.delete(IDBKeyRange.lowerBound(0));
	store.delete(IDBKeyRange.upperBound(0));
	</pre>
        <div class="api-info">
            <h3>Error (IDBDatabaseException)</h3>
            <p>In the IndexedDB API, an IDBDatabaseException object represents exception conditions that can be encountered while performing database operations.</p>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>code</td>
                <td>This property represents the number of the error.</td>
            </tr>
            <tr>
                <td>message</td>
                <td>This property returns a message describing the error.</td>
            </tr>
        </table>
    <pre class="api-code">
	var trans = $$.db.transaction([object_name], "readwrite");
	var store = trans.objectStore(object_name);
	var cursorRequest = store.openCursor();
	cursorRequest.onerror=function(e){
	   console.log(e);
	} 
	</pre>
        <h4 class="api-table-caption">The Error values</h4>
        <table class="api-table">
            <tr>
                <td>0</td>
                <td>UNKNOWN_ERR</td>
            </tr>
            <tr>
                <td>1</td>
                <td>NON_TRANSIENT_ERR</td>
            </tr>
            <tr>
                <td>2</td>
                <td>NOT_FOUND_ERR</td>
            </tr>
            <tr>
                <td>3</td>
                <td>CONSTRAINT_ERR</td>
            </tr>
            <tr>
                <td>4</td>
                <td>DATA_ERR</td>
            </tr>
            <tr>
                <td>5</td>
                <td>NOT_ALLOWED_ERR</td>
            </tr>
            <tr>
                <td>6</td>
                <td>TRANSACTION_INACTIVE_ERR</td>
            </tr>
            <tr>
                <td>7</td>
                <td>ABORT_ERR</td>
            </tr>
            <tr>
                <td>11</td>
                <td>READ_ONLY_ERR</td>
            </tr>
            <tr>
                <td>21</td>
                <td>RECOVERABLE_ERR</td>
            </tr>
            <tr>
                <td>31</td>
                <td>TRANSIENT_ERR</td>
            </tr>
            <tr>
                <td>32</td>
                <td>TIMEOUT_ERR</td>
            </tr>
            <tr>
                <td>33</td>
                <td>DEADLOCK_ERR</td>
            </tr>
        </table>
    </div>
</template>
<script>
export default {
    name: 'indexeddb',
    data() {
        return {}
    }
}
</script>



